@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap);
* {
  box-sizing: border-box; }

body {
  margin: 0;
  font-family: "Roboto", sans-serif; }

input:focus,
select:focus,
textarea:focus,
button:focus {
  outline: none; }

.NavBar {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1;
  background-color: #ff8800;
  overflow: hidden; }
  .NavBar a {
    float: left;
    display: block;
    color: #f2f2f2;
    text-align: center;
    text-decoration: none;
    font-size: 17px; }
  .NavBar .title {
    color: white;
    padding: 0;
    margin: 0;
    letter-spacing: -0.06em;
    font-weight: 700;
    text-shadow: 0px 5px 20px #00000025; }
  .NavBar .search-box {
    float: left;
    display: block;
    margin: 1rem auto;
    font-size: 0.9em;
    border-radius: 5px;
    border: 0; }
  .NavBar .form-inline {
    text-align: right; }
  .NavBar .navbar-toggler {
    border: 0; }
  .NavBar .navbar-dark,
  .NavBar .navbar-toggler-icon {
    background-image: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAB4AAAAeCAQAAACROWYpAAAAAmJLR0QA/4ePzL8AAAAkSURBVEjHY2AYBUMJ/D/8n1RwmBLNh0aDfDSqRqNqNKpGNAAA/lk0BY7e7+4AAAAASUVORK5CYII="); }
  @media only screen and (max-width: 575px) {
    .NavBar .form-group {
      width: 100%; } }
  .NavBar label {
    padding: 1rem;
    color: #f2f2f2;
    text-align: right; }
  .NavBar select,
  .NavBar input {
    font-size: 0.9em;
    border-radius: 5px;
    border: 1px solid #d17000; }

.grid-container {
  margin: auto;
  margin: 6rem 0; }

.card-grid {
  margin: auto;
  display: grid;
  align-items: center;
  grid-gap: 10px;
  justify-content: center; }

.card {
  border: 0;
  padding: 0.4rem;
  box-shadow: 0px 1px 6px 1px rgba(0, 0, 0, 0.226);
  background-color: white; }
  .card .card-body {
    padding: 0.5rem; }

@media only screen and (max-width: 740px) {
  .card {
    width: 100%; }
  .card-grid {
    grid-template-columns: repeat(1, 95%);
    grid-gap: 0.8rem; } }

@media only screen and (min-width: 740px) and (max-width: 1024px) {
  .card {
    width: 100%; }
  .card-grid {
    grid-template-columns: repeat(2, 45%); } }

@media only screen and (min-width: 1000px) {
  .card {
    width: 100%; }
  .card-grid {
    grid-template-columns: repeat(3, 20rem); } }

.card-title {
  font-weight: 700;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden; }

.fav-btn {
  border: 0;
  background-color: #00000000;
  width: 2rem;
  float: right; }

.status-icon {
  height: 1.2rem;
  margin-right: 0.5rem;
  vertical-align: text-bottom; }

.order-btn {
  width: 100%;
  background-color: #ff8800;
  border: 0px;
  border-radius: 3px;
  padding: 8px 14px;
  color: white; }

.closed {
  background-color: #bebebe; }

